body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.sk-panel__content {
  max-height: 300px;
  overflow-y: scroll;
  padding-right: 10px;
}

.panel {
  border: 1px solid #f4f4f4;
  border-radius: 4px;
  -webkit-box-shadow: inset 0 1px 1px rgba(0,0,0,0.05);
          box-shadow: inset 0 1px 1px rgba(0,0,0,0.05);
  color: #666;
  font-family: "Helvetica Neue",Helvetica,Arial,sans-serif;
  font-size: 14px;
  line-height: 1.42857143;
  margin: 0 15px 20px;
}

.panel a {
  text-decoration: none;
}

.panel .panel-heading {
  color: #08c;
  background-color: #f4f4f4;
  display: -ms-flexbox;
  display: flex;
  padding: 15px;
}

.panel .panel-heading > * {
  -ms-flex: auto;
      flex: auto;
  text-align: center;
}

.panel .panel-heading > *:first-child {
  text-align: left;
}

.panel .panel-heading > *:last-child {
  text-align: right;
}

.panel .panel-body {
  padding: 15px;
}

.panel .panel-body table {
  border-collapse: collapse;
  border-spacing: 0;
  margin-bottom: 20px;
  width: 100%;
}

.panel .panel-body table th {
  border-bottom: 2px solid #f4f4f4;
  text-align: left;
  vertical-align: bottom;
}

.panel .panel-body h4 {
  font-size: 18px;
  font-family: "Helvetica Neue",Helvetica,Arial,sans-serif;
  font-weight: 500;
  line-height: 1.1;
  margin-bottom: 10px;
  margin-top: 10px;
}



